<script>
import {
    mapGetters
} from "vuex";
import dashBoard from './Home/Dashboard.vue'
import addCustomerGroup from "./admin/AddCustomerGroup.vue"
export default {
    components: {
        dashBoard,
        addCustomerGroup,
    },
    data() {
        return {
            F_name: "",
            darkMode: false,
            theme: "",
            userName: "",
        };
    },
    methods: {
        logout() {
            this.$confirm(
                "Are you sure you want to logout?",
                "Logout",
                "question"
            ).then(() => {
                localStorage.setItem("token", null);
                localStorage.setItem("user", null);
                localStorage.setItem("login", false);

                console.log(localStorage.getItem("token"));
                console.log(localStorage.getItem("login"));
                this.$router.push("/");
            });
        },
        // toggleBodyClass(addRemoveClass, className) {
        //     const el = document.body;

        //     if (addRemoveClass === 'addClass') {
        //         el.classList.add(className);
        //     } else {
        //         el.classList.remove(className);
        //     }
        // },
        changeTheme() {
            console.log(this.darkMode);
            const el = document.body;

            if (this.darkMode === true) {
                el.classList.add('dark-mode');
                this.theme = "Dark"
            } else {
                el.classList.remove('dark-mode');
                this.theme = "Light"
            }
        }
    },
    beforeMount() {
        this.F_name = localStorage.getItem("user");
        this.userName = localStorage.getItem("user");
    },

    computed: {
        ...mapGetters(["user"]),
    },
    mounted() {
        console.log(localStorage.getItem("login"));

        if (localStorage.getItem("login") == "false" || localStorage.getItem("token") == null) {
            console.log(localStorage.getItem("login") + "ttttt");
            this.$router.push("/");
        }

        this.changeTheme();
    },

    destroyed() {
        this.changeTheme();
    }
};
</script>


