<template>
  <canvas ref="doughChart" :width="width" :height="height"></canvas>
</template>

<script>
import Chart from 'chart.js';

Chart.defaults.global.defaultFontFamily  = 'Source Sans Pro';
export default {
  name: 'top-group-sales',
  props: {
    // The canvas's width.
    width: {
      type: Number,
      validator: value => value > 0
    },

    // The canvas's height.
    height: {
      type: Number,
      validator: value => value > 0
    },

    // The chart's data.labels
    labels: Array,

    // The chart's data.datasets
    datasets: {
      type: Array,
      required: true
    },
    

    // The chart's options.
    options: Object
  },
  mounted() {
    new Chart(this.$refs.doughChart, {
      type: 'doughnut',
        
      data: {
        labels: this.labels,
        datasets: this.datasets,
          
      },
      options: this.options
    });
  }
}
</script>