<script>
import numeral from 'numeral';
import LineChart from './LineChart';
import DoughnutChart from './DougnutChart';
import ChartDataLabels from 'chartjs-plugin-labels'

const options = {
    scales: {
        // xAxes: [{
        //     stacked: false,
        //     categoryPercentage: 0.85,
        //     barPercentage: 0.85,
        //     gridLines: {
        //         display: true
        //     },
        //     fontSize: 6
        // }],
        yAxes: [{
            ticks: {
                beginAtZero: false,
                callback: value => numeral(value).format('Rs 0,0')
            }
        }],

    },
    tooltips: {
        mode: 'label',
        callbacks: {
            label(tooltipItem, data) {
                const label = data.datasets[tooltipItem.datasetIndex].label;
                const value = numeral(tooltipItem.yLabel).format('Rs 0,0');

                return `${label}: ${value}`;
            }
        }
    }
};

export default {
    name: "dashBoard",
    options,

    components: {
        LineChart,
        //BarChart,
        DoughnutChart
    },

    data() {
        return {
            customerCount: 0,
            receiptCount: 0,
            grossSales: 0,
            saleQty: 0,
            sales: [],
            salesDates: [],
            purchase: [],
            purchDates: [],
            months: [],
            year: '',
            chartData: null,
            loaded: false,
            doughData: null,
            groupSales: [],
            groupNames: [],
            coloR: [],
            efficiency: [],
            doughnutOpt: null
        }
    },

    methods: {
        fetchCardData() {
            this.$loading(true);
            this.$store.dispatch("dashboard/fetchDashboardCardData", null, {
                root: true
            }).then(() => {
                var data = this.$store.state.dashboard.dashCardData;
                this.customerCount = data[0].customerCount
                this.receiptCount = data[0].receiptCount
                this.grossSales = data[0].grossSales
                this.saleQty = data[0].saleQty
            });
            this.flag = false;

            this.$loading(false);
        },

        fetchMonthRecapData() {
            this.loaded = false;
            this.$store.dispatch("dashboard/fetchDashMonthRecapData", null, {
                root: true
            }).then((data) => {
                data.sales.forEach(e => {
                    this.sales.push(e.Amount)
                    this.salesDates.push(e.Date)
                });
                data.purchase.forEach(e => {
                    this.purchase.push(e.Amount)
                    this.purchDates.push(e.Date)
                });
                data.groupSale.forEach(e => {
                    this.groupNames.push(e.Group)
                    this.groupSales.push(e.Amount)
                });
                this.loaded = true;
            });

            this.chartData = {
                labels: this.salesDates,
                datasets: [

                    {
                        label: 'Sales',
                        backgroundColor: 'rgba(210, 214, 222, 1)',
                        borderColor: 'rgba(210, 214, 222, 1)',
                        fill: true,
                        // pointRadius: false,
                        //    pointColor: 'rgba(210, 214, 222, 1)',
                        // pointStrokeColor: '#c1c7d1',
                        //  pointHighlightFill: '#fff',
                        // pointHighlightStroke: 'rgba(220,220,220,1)',
                        data: this.sales
                    },
                    {
                        label: 'Purchase',
                        backgroundColor: 'rgba(60,141,188,0.9)',
                        borderColor: 'rgba(60,141,188,0.8)',
                        fill: true,
                        pointColor: '#3b8bba',
                        data: this.purchase
                    },

                ]
            }

            this.doughData = {
                labels: this.groupNames,
                datasets: [

                    {
                        label: 'Group Sales Analyse',
                        backgroundColor: ['#00a65a', '#fffff', '#00c0ef', '#d2d6de', '#f49c12', '#01c0ef', '#3c6dbc', '#d2d1de', '#0ff5de', '#f56954'],
                        hoverOffset: 4,
                        fill: true,
                        data: this.groupSales
                    },

                ]
            }

            this.setOptions();
        },

        setOptions() {
            this.doughnutOpt = {
                tooltips: {
                    enabled: true
                },
                pieceLabel: {
                    render: 'label',
                    arc: true,
                    fontColor: '#fff',
                    position: 'outside'
                },
                responsive: true,
                maintainAspectRatio: true,
                plugins: {
                    labels: {
                        render: 'percentage',
                        fontColor: ['green', 'white', 'red'],
                        precision: 2
                    }
                },

                legend: {
                    position: 'bottom',
                },
                // title: {
                //     display: true,
                //     text: 'Testing',
                //     fontSize: 20
                // },
                animation: {
                    animateScale: true,
                    animateRotate: true
                },
                // plugins: [ChartDataLabels],
                // plugins: {
                //     labels: {
                //         color: '#ffffff',
                //         formatter: (value) => {
                //             return value + '%'
                //         },
                //           position: 'outside'
                //     }

                //     // labels: {
                //     //     render: 'label',
                //     //     arc: true,
                //     //     fontColor: '#000',
                //     //     position: 'outside'
                //     // }
                // },
            }
        }

    },
    created() {

        this.fetchCardData();
        this.fetchMonthRecapData();
    },

};
</script>

